.events {
  margin-bottom: 100px;
}

.events__slider-wrapper {
  position: relative;

  .slider-btn {
    top: 50%;
    transform: translateY(-50%);
  }
}

.events__slider-container {
  overflow: hidden;
  padding: 25px;
}

.events__slider {
  @include list-reset;
  display: flex;
}

.events__slide {
  flex-shrink: 0;
  border-radius: 2px;
  background-color: rgba(39, 40, 42, 0.85);
  box-shadow: 0px 0px 45px 0px rgba(0, 0, 0, 0.35);

  a {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 32px 22px 24px;
    text-decoration: none;
    @include not-hover;
    color: #ffffff;

    &:hover {
      @include hover;
    }
  }
}

.events__slide-date {
  margin: 0;
  padding-bottom: 23px;
  @include font(16, 19);
  border-bottom: 1px solid rgba(239, 232, 216, 0.10);

  span {
    @include font(60, 70);
    font-weight: 200;
  }
}

.events__slide-day {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 23px;
  padding: 10px 0;
  @include font(16, 19);
  border-bottom: 1px solid rgba(239, 232, 216, 0.10);

  span {
    margin-right: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.events__slide-title {
  margin: 0;
  margin-bottom: 58px;
  @include font(20, 26);
  color: $golden;
}

.events__details-link {
  margin: 0;
  @include font(16, 19);
}

@media (max-width: $desktop-width) {
  .events {
    margin-bottom: 70px;
  }
}

@media (max-width: $tablet-width) {
  .events {
    margin-bottom: 50px;
  }
}
