.reservation {
  padding-top: 100px;
  margin-bottom: 100px;

  .section-title {
    margin: 0;

    &--mobile {
      display: none;
    }
  }
}

.reservation__form {
  display: flex;
  flex-direction: column;
}

.reservation__form-content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1145px;
  margin: 0 auto 50px;
  padding: 79px 100px;
  background-color: #27282a;
  box-shadow: 0 0 45px 0 rgba(0, 0, 0, 0.35);

  p {
    width: 42.2%;
    max-width: 352px;
    margin: 0;
    margin-right: 5.1%;
    @include font(20, 26);
  }
}

.reservation__form-fields {
  display: flex;
  flex-direction: column;
  width: 52.7%;

  input,
  textarea {
    @include font(16 ,19);
    @include not-hover;
    width: 100%;
    margin-bottom: 20px;
    padding: 16px;
    background-color: #27282a;
    border: 1px solid rgba(196, 164, 120, 0.10);
    color: #efe8d8;

    &:last-child {
      margin-bottom: 0;
    }

    &::placeholder {
      @include font(16, 19);
      color: #efe8d8;
      opacity: 0.2;
    }

    @media (min-width: 1025px)  {
      &:hover {
        @include hover;
        border-color: $golden;
      }
    }

    &:focus {
      @include hover;
      border-color: $golden;
    }
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    border-radius: 0;
    margin: 0;

    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }

  input[type=number]::-moz-inner-spin-button,
  input[type=number]::-moz-outer-spin-button {
    margin: 0;

    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }

  input[type=number]::-ms-inner-spin-button,
  input[type=number]::-ms-outer-spin-button {
    margin: 0;

    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }

  input[type=number] {
    outline: none;
    appearance: none;
    -webkit-appearance: none;
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-appearance: textfield;
  }

}

.reservation__form-submit {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 150px;

  .button {
    margin-bottom: 30px;
  }

  p {
    max-width: 580px;
    margin: 0;
    @include font(16, 19);
    color: $golden;
    text-align: center;
    opacity: 0.3;

    a {
      text-decoration: none;
    }
  }

  .link-down {
    position: absolute;
    right: 0;
    bottom: 0;
    flex-direction: row-reverse;
    text-align: right;

    svg {
      margin-right: 0;
      margin-left: 26px;
    }
  }
}

@media (max-width: $desktop-width) {

  .reservation {
    padding-top: 70px;
    margin-bottom: 70px;

    .section-title {
      display: none;

      &--mobile {
        display: block;
      }
    }
  }

  .reservation__form-content {
    padding: 50px 40px;
  }
}

@media (max-width: $tablet-width) {
  .reservation {
    padding-top: 50px;
    margin-bottom: 50px;

    .section-header {
      margin-bottom: 36px;
    }
  }

  .reservation__form-content {
    flex-direction: column;
    margin-bottom: 30px;
    padding: 46px 20px;

    p {
      width: 100%;
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 60px;
      @include font(16, 19);
    }
  }

  .reservation__form-fields {
    width: 100%;
  }

  .reservation__form-submit {
    padding: 0;

    .button {
      width: 100%;
    }

    p {
      max-width: 100%;
    }

    .link-down {
      position: static;
      flex-direction: row;
      margin-top: 60px;
      text-align: left;

      svg {
        margin-left: 0;
        margin-right: 26px;
      }
    }
  }
}
